  display: flex;
  flex-direction: column;
  padding: 20px;
}

.resume .resume_left {
  width: 245px;
  background: #484444;
}

.resume .resume_left .resume_profile {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  overflow: hidden;
  margin: 20px auto;
}

.resume .resume_left .resume_profile img {
  width: 100%;
  height: 100%;
}

.resume .resume_left .resume_content {
  padding: 0 15px;
}

.resume .top-padding {
  margin-top: 20px;
}

.resume .resume_left .bold {
