}

.resume .resume_left .regular {
  color: #fff;
}

.resume .resume_item {
  padding: 25px 0;
  border-bottom: 2px solid #837c7c3b;
}

.resume .resume_item1 {
  padding-top: 25px;
  border-bottom: 2px solid #837c7c3b;
}

.resume .resume_left .resume_item:last-child,
.resume .resume_right .resume_item:last-child {
  border-bottom: 0px;
}

.resume .resume_left ul li {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}

.resume .resume_left ul li:last-child {
  margin-bottom: 0;
}

.resume .resume_left ul li .icon {
  min-width: 20px;
  width: 20px;
  height: 20px;
  color: #fff;
  border-radius: 50%;
  margin-right: 5px;
  font-size: 16px;
  position: relative;
}

.resume .icon i,
.resume .resume_right .resume_hobby ul li i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
}

.resume .resume_left ul li .data {
  color: #fff;
}

.resume .resume_left ul li .data a {
  color: #fff;
  text-decoration: unset;
}

.resume .resume_left .resume_skills ul li {
  display: flex;
  margin-bottom: 10px;
  color: #fff;
  justify-content: space-between;
  align-items: center;
}

.resume .resume_left .resume_skills ul li .skill_progress {
  width: 60%;
  margin: 0 5px;
  height: 5px;
  background: #009fd9;
  position: relative;
}

.resume .resume_left .resume_skills ul li .skill_per {
  width: 50;
}

.resume .resume_left .resume_skills ul li .skill_progress span {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: #fff;
}

.resume .resume_left .resume_social .semi-bold {
  color: #fff;
  margin-bottom: 3px;
}

.resume .resume_right {
  width: 551px;
  padding: 15px;
}

.resume .resume_right .bold {
  color: #484444;
  font-family: "Archivo Narrow", sans-serif !important;
}

.resume .resume_right .resume_work ul,
.resume .resume_right .resume_education ul {
  padding-left: 30px;
  overflow: hidden;
}

.resume .resume_right ul li {
  position: relative;
}

.resume .resume_right ul li .date {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 15px;
}

.resume .resume_right .projectList {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 15px;
  padding-left: 30px;
}

.resume .resume_right .projectListInfo {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 15px;
  padding-left: 30px;
}

.resume .resume_right ul li .info {
  margin-bottom: 20px;
}

.resume .resume_right ul li:last-child .info {
  margin-bottom: 0;
}

.main_skill {
  font-weight: 600;
  display: flex;
  align-items: center;
}

.main_skill .line-div {
  height: 1px;
  background-color: #000;
  display: none;
  flex-grow: 1;
  margin-left: 20px;
}

.resume .resume_right .resume_work .CompanyHeader li:before,
.resume .resume_right .resume_education .CompanyHeader li:before {
  content: "";
