  top: 8px;
  left: -24px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  border: 2px solid #484444;
}

.resume .resume_right .resume_hobby ul {
  display: flex;
  justify-content: space-between;
}

.resume .resume_right .resume_hobby ul li {
  width: 80px;
