  border: 2px solid #484444;
  border-radius: 50%;
  position: relative;
  color: #484444;
}

.resume .resume_right .resume_hobby ul li i {
  font-size: 30px;
}

.resume .resume_right .resume_hobby ul li:before {
  content: "";
  position: absolute;
  top: 40px;
  right: -52px;
  width: 50px;
  height: 2px;
  background: #484444;
}

.resume .resume_right .resume_hobby ul li:last-child:before {
  display: none;
}

